<template>
  <div class="payList">
    <avue-crud :option="option"
               :data="list"
               :page="page"
               :row-style="rowStyle"
               :cell-style="cellStyle"
               @current-change="currentChange"
               @search-change="searchChange"
               @search-reset="refreshChange"
               @size-change="sizeChange"
               @refresh-change="refreshChange">
      <template slot="header">
        <avue-data-box :option="dataOption"/>
      </template>
      <template slot="menuLeft">
        <el-button type="danger"
                   :icon="el-icon-document"
                   @click="$router.push({path:'/fp'})"
                   size="small">发票管理
        </el-button>
        <el-button type="primary"
                   @click="handleTime"
                   size="small">{{ time === 0 ? '刷 新' : '停 止' }}
        </el-button>
      </template>
      <template slot-scope="scope" slot="type">
        <img width="30"
             :src="scope.row.type === 0 ? '/images/zfbicon.png' : (scope.row.type === 1 ? '/images/wxicon.png' : '/images/zgyhicon.png')">
      </template>
      <template slot="menu"
                slot-scope="scope">
        <template v-if="!scope.row.flag">
          <el-button type="text"
                     @click="handleOk(scope.row)"
                     size="small">开通
          </el-button>
          <el-button type="text"
                     @click="handleDel(scope.row)"
                     size="small">删除
          </el-button>
        </template>
        <template v-else>
          <el-button type="text"
                     @click="handleReset(scope.row)"
                     size="small">重置
          </el-button>
        </template>
        <el-button type="text" v-if="scope.row.version!==0"
                   @click="handleFp(scope.row)"
                   size="small">发票
        </el-button>
        <el-button type="text" v-if="scope.row.version===3"
                   @click="handlePush(scope.row)"
                   size="small">推送
        </el-button>
        <el-button type="text" v-if="scope.row.version===3"
                   @click="handlePushAvue(scope.row)"
                   size="small">开通Avue
        </el-button>
      </template>
    </avue-crud>
    <el-dialog title="发票信息"
               top="40px"
               :before-close="beforeClose"
               :visible.sync="fpbox"
               width="500px">
      <avue-form :option="fpoption"
                 @submit="fpSubmit"
                 v-model="fpform">
        <template slot="compid"
                  slot-scope="{}">
          <el-input placeholder="请输入编号"
                    v-model="fpform.compid">
            <div slot="append" style="cursor: pointer"
                 @click="buildId">自动生成编号
            </div>
          </el-input>
        </template>
        <template slot="menuForm">
          <el-button @click="fpBuild">生成授权书</el-button>
        </template>
      </avue-form>
    </el-dialog>
    <auth ref="auth"
          @submit="getList">
    </auth>
  </div>
</template>

<script>
import {config, payList} from './config'
import {mapState} from 'vuex';

export default {
  data() {
    return {
      moneyData: [],
      page: {
        currentPage: 1,
        total: 0,
        pageSizes: [10, 30, 50, 100, 200, 1000],
        pageSize: 10
      },
      check: 0,
      flag: false,
      time: 0,
      fpbox: false,
      version: 0,
      fpform: {
        qq: ''
      },
      fpoption: {
        size: 'mini',
        emptyBtn: false,
        column: [
          {
            label: '订单号',
            prop: 'orderid',
            span: 24
          }, {
            label: '编号',
            prop: 'compid',
            formslot: true,
            span: 24
          }, {
            label: '开票金额',
            prop: 'money',
            rules: [{
              required: true,
              message: "请输入开票金额",
              trigger: "blur"
            }],
            span: 24
          }, {
            label: '展示名称',
            prop: 'zsmc',
            rules: [{
              required: true,
              message: "请输入展示名称",
              trigger: "blur"
            }],
            span: 24
          }, {
            label: '公司名称',
            prop: 'mc',
            rules: [{
              required: true,
              message: "请输入公司名称",
              trigger: "blur"
            }],
            span: 24
          }, {
            label: '公司税号',
            prop: 'sh',
            rules: [{
              required: true,
              message: "请输入公司税号",
              trigger: "blur"
            }],
            span: 24
          }
          /*, {
            label: '收件人',
            prop: 'sjr',
            span: 24
          }, {
            label: '联系方式',
            prop: 'lxfs',
            span: 24
          }, {
            label: '收件地址',
            prop: 'sjdz',
            type: 'textarea',
            span: 24
          }*/
          , {
            label: '备注',
            prop: 'bz',
            span: 24
          }]
      },
      option: {
        size: 'mini',
        excelBtn: true,
        selection: true,
        addBtn: false,
        editBtn: false,
        delBtn: false,
        index: true,
        tip: false,
        headerAlign: 'center',
        menuAlign: 'left',
        align: 'center',
        sumColumnList: [
          {
            name: 'money',
            type: 'sum'
          },
          {
            name: 'version',
            type: 'count'
          }
        ],
        defaultSort: {
          prop: 'datetime',
          order: 'descending'
        },
        column: [
          {
            width: 200,
            label: '订单号',
            prop: 'orderid',
            search: true
          }, {
            label: '套餐名',
            prop: 'version',
            type: 'select',
            dicData: config,
            filter: true,
          }, {
            label: '金额',
            prop: 'money'
          }, {
            label: '备注QQ',
            prop: 'qq',
            search: true
          }, {
            label: '支付类型',
            prop: 'type',
            type: 'select',
            dicData: payList,
            filter: true,
            slot: true,
          }, {
            label: '状态',
            prop: 'flag',
            type: 'select',
            filter: true,
            dicData: [{
              label: '已付款',
              value: true
            }, {
              label: '未确认',
              value: false
            }]
          }, {
            label: '下单日期',
            sortable: true,
            prop: 'datetime',
          }
        ]
      },
      list: [],
    }
  },
  computed: {
    dataOption() {
      return {
        span: 4,
        data: [
          {
            title: '总单数',
            count: this.moneyData[0] || 0,
            icon: 'el-icon-s-check',
            color: '#00a680',
            target: '_blank'
          },
          {
            title: '总金额',
            count: this.moneyData[1] || 0,
            icon: 'el-icon-s-order',
            color: '#E6A23C',
            target: '_blank'
          }, {
            title: '上月金额',
            count: this.moneyData[2] || 0,
            icon: 'el-icon-view',
            color: 'rgb(56, 161, 242)',
            target: '_blank'
          },
          {
            title: '本月金额',
            count: this.moneyData[3] || 0,
            icon: 'el-icon-setting',
            color: 'rgb(117, 56, 199)',
            target: '_blank'
          },
          {
            title: '今日金额',
            count: this.moneyData[4] || 0,
            icon: 'el-icon-tickets',
            color: '#F56C6C',
            target: '_blank'
          },
          {
            title: '今日单数',
            count: this.moneyData[5] || 0,
            icon: 'el-icon-user',
            color: 'rgb(97, 60, 189)',
            target: '_blank'
          },
        ]
      }
    },
    ...mapState(['pwd']),
  },
  methods: {
    buildId() {
      const query = this.Bmob.Query("ticket");
      query.equalTo("special", "!=", true);
      query.equalTo("compid", "!=", '');
      query.order("-datetime", "-compid");
      query.limit(1)
      query.find().then(res => {
        const key = 'BLADE-', len = 6;
        let id = Number(res[0].compid.replace(/[^0-9]/ig, "")) + 1;
        let length = len - (id + '').length;
        let result = '';
        for (let i = 0; i < length; i++) {
          result = result + '0'
        }
        result = key + result + id;
        this.fpform.compid = result;
        this.$message.success('生成成功')
      });
    },
    getMoneyData() {
      let query = this.Bmob.Query("order");
      const date = new Date();
      let month = date.getMonth() + 1;
      month = month < 10 ? '0' + month : month
      let lastmonth = date.getMonth();
      lastmonth = lastmonth < 10 ? '0' + lastmonth : lastmonth
      let year = date.getFullYear();
      let lastyear = year;
      if (lastmonth === "00") {
        lastyear = year - 1;
        lastmonth = "12";
      }
      //总订单
      query = this.Bmob.Query("order");
      query.equalTo("flag", "==", true);
      query.count().then(res => {
        this.$set(this.moneyData, 0, res)
      })
      // 总金额
      query.equalTo("flag", "==", true);
      query.statTo("sum", "money");
      query.find().then(res => {
        this.$set(this.moneyData, 1, res[0]._sumMoney)
      });
      //上月金额
      query = this.Bmob.Query("order");
      query.equalTo("datetime", ">=", `${lastyear}-${lastmonth}-01`);
      query.equalTo("datetime", "<=", `${lastyear}-${lastmonth}-31`);
      query.equalTo("flag", "==", true);
      query.statTo("sum", "money");
      query.find().then(res => {
        this.$set(this.moneyData, 2, res[0]._sumMoney)
      });
      //本月金额
      query = this.Bmob.Query("order");
      query.equalTo("datetime", ">=", `${year}-${month}-01`);
      query.equalTo("datetime", "<=", `${year}-${month}-31`);
      query.equalTo("flag", "==", true);
      query.statTo("sum", "money");
      query.find().then(res => {
        this.$set(this.moneyData, 3, res[0]._sumMoney)
      });
      //今日金额
      query = this.Bmob.Query("order");
      query.equalTo("datetime", ">=", (new Date()).Format('yyyy-MM-dd'));
      query.equalTo("flag", "==", true);
      query.statTo("sum", "money");
      query.find().then(res => {
        this.$set(this.moneyData, 4, res[0]._sumMoney)
      });
      //今日订单
      query = this.Bmob.Query("order");
      query.equalTo("flag", "==", true);
      query.equalTo("datetime", ">=", (new Date()).Format('yyyy-MM-dd'));
      query.count().then(res => {
        this.$set(this.moneyData, 5, res)
      })
    },
    currentChange(val) {
      this.page.currentPage = val;
      this.getList()
    },
    searchChange(params) {
      this.getList(params);
    },
    sizeChange(pageSize) {
      this.page.pageSize = pageSize;
      this.getList()
    },
    insert(data, table) {
      const query = this.Bmob.Query(table || 'order');
      Object.keys(data).forEach(ele => {
        if (ele.includes('$')) {
          query.set(ele.replace('$', '') + '_text', data[ele])
        } else {
          query.set(ele, data[ele])
        }
      })
      return query.save()
    },
    fpBuild() {
      const {href} = this.$router.resolve({
        path: '/sqs',
        query: {
          qq: this.fpform.qq,
          id: this.fpform.compid,
          name: this.fpform.mc,
          date: this.fpform.datetime
        }
      })
      window.open(href, '_blank')
    },
    fpSubmit(form, done) {
      form = this.deepClone(this.fpform);
      if (!form.datetime) form.datetime = (new Date()).Format('yyyy.MM.dd');
      if (form.objectId) {
        form.money = form.money + '';
        delete form.flag;
        this.update(form.objectId, form, 'ticket').then(() => {
          this.fpbox = false;
          this.$message.success('发票修改成功');
          done();
        })
      } else {
        delete form.objectId;
        delete form.createdAt;
        delete form.updatedAt;
        form.flag = false;
        this.insert(form, 'ticket').then(() => {
          this.fpbox = false;
          this.$message.success('发票提交成功');
          done();
        })
      }
    },
    beforeClose(done) {
      this.$set(this.fpform, {})
      done()
    },
    handleFp(row) {
      this.version = row.version;
      const query = this.Bmob.Query("ticket");
      query.equalTo("orderid", "==", row.orderid);
      query.find().then(res => {
        if (this.validatenull(res[0])) {
          delete this.fpform.objectId;
          this.fpform.compid = 'BLADE-';
          this.fpform.orderid = row.orderid;
          this.fpform.money = row.money + '';
          this.fpform.qq = row.qq;
          this.fpform.zsmc = '';
          this.fpform.mc = '';
          this.fpform.sh = '';
          this.fpform.sjr = '';
          this.fpform.lxfs = '';
          this.fpform.sjdz = '';
          this.fpbox = true;
          this.$alert(`暂无提供发票信息`, '提示', {
            confirmButtonText: '确定',
          });
        } else {
          this.fpform = res[0];
          this.fpform.qq = row.qq;
          this.fpbox = true
        }

      });
    },
    update(id, data, table) {
      const form = this.deepClone(data);
      const query = this.Bmob.Query(table || 'order');
      query.set('id', id)
      delete form.createdAt;
      delete form.updatedAt;
      delete form.objectId;
      Object.keys(form).forEach(ele => {
        if (form[ele]) {
          query.set(ele, form[ele]) //需要修改的objectId
        }
      })
      return query.save()
    },
    del(row) {
      const query = this.Bmob.Query('order');
      query.destroy(row.objectId);
      const queryTicket = this.Bmob.Query("ticket");
      queryTicket.equalTo("orderid", "==", row.orderid);
      queryTicket.find().then(res => {
        queryTicket.destroy(res[0].objectId);
      })
    },
    rowStyle({row}) {
      if (!row.flag) {
        return {
          backgroundColor: 'rgba(245,108,108,0.1)'
        }
      }
    },
    cellStyle({row, columnIndex}) {
      if (columnIndex == 7) {
        return Object.assign((() => {
          return {color: row.type === 0 ? '#409EFF' : (row.type === 1 ? 'green' : 'red'),}
        })(), {
          fontWeight: 'bold',
          fontSize: '20'
        })

      } else if (columnIndex == 8) {
        if (!row.flag) {
          return {
            color: 'red',
            fontWeight: 'bold',
            fontSize: '20'
          }
        }
      } else if (columnIndex == 2) {
        return {
          color: '#333',
          fontWeight: 'bold',
          fontSize: '20'
        }
      }
    },
    handleDel(row) {
      this.$confirm(`是否删除<<${row.qq}>>的<<${row.name}>>订单`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        return this.del(row)
      }).then(() => {
        this.list.splice(row.$index, 1);
        this.$message.success('删除成功')
      })
    },
    handleReset(row) {
      this.$confirm(`是否重置<<${row.qq}>>的<<${row.name}>>权限`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        if (row.version !== 0) {
          // 企业版
          return this.$httpajax.get(`${this.website.GIT.url}&qq=${row.qq}&type=blade-enterprise`)
        } else {
          // 专业版
          return this.$httpajax.get(`${this.website.GIT.url}&qq=${row.qq}&type=blade-code`)
        }
      }).then((res) => {
        this.$alert(`${res.data}`, '提示', {
          confirmButtonText: '确定',
        });
      })
    },
    handleOk(row) {
      this.$confirm(`是否更新<<${row.qq}>>的<<${row.name}>>权限`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        row.flag = true
        return this.update(row.objectId, {
          flag: true
        })
      }).then(() => {
        return this.$confirm(`是否开通<<${row.qq}>>的<<${row.name}>>权限`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
      }).then(() => {
        if (row.version !== 0) {
          // 企业版
          return this.$httpajax.get(`${this.website.GIT.url}&qq=${row.qq}&type=blade-enterprise`)
        } else {
          // 专业版
          return this.$httpajax.get(`${this.website.GIT.url}&qq=${row.qq}&type=blade-code`)
        }
      }).then((res) => {
        this.$alert(`${res.data}`, '提示', {
          confirmButtonText: '确定',
        });
      })
    },
    handlePush(row) {
      this.$confirm(`是否推送<<${row.qq}>>的<<${row.name}>>信息至avue系统`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.version = row.version;
        const query = this.Bmob.Query("ticket");
        query.equalTo("orderid", "==", row.orderid);
        query.find().then(res => {
          if (this.validatenull(res[0])) {
            this.$alert(`暂无发票信息，无法推送`, '提示', {
              confirmButtonText: '确定',
            });
          } else {
            this.fpform = res[0];
            this.fpform.qq = row.qq;
            // 推送avue订单信息
            let data = this.deepClone(row);
            data.flag = true;
            delete data.objectId;
            delete data.createdAt;
            delete data.updatedAt;
            this.$httpajax.post('https://avuejs.com/sysdata?type=0', Object.assign(data, {
              money: 1650,
              version: 10
            }));
            // 推送avue发票信息
            this.fpform.flag = true;
            delete this.fpform.compid;
            delete this.fpform.zsmc;
            delete this.fpform.money;
            delete this.fpform.objectId;
            delete this.fpform.createdAt;
            delete this.fpform.updatedAt;
            delete this.fpform.bz;
            this.$httpajax.post('https://avuejs.com/sysdata?type=1', this.fpform);
            this.dingPush(row);
          }
        });
      })
    },
    handlePushAvue(row) {
      this.$confirm(`是否开通<<${row.qq}>>的<<${row.name}>>Avue权限`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$httpajax.get(`${this.website.GIT.avueUrl}&qq=${row.qq}`).then((res) => {
          this.$alert(`${res.data}`, '提示', {
            confirmButtonText: '确定',
          });
        });
      })
    },
    handleTime() {
      if (this.time == 0) {
        this.time = 1000;
        this.check = setInterval(() => {
          this.getList()
        }, this.time);

      } else {
        this.time = 0;
        clearInterval(this.check)
      }
    },
    dingPush(row) {
      this.$httpajax.post('https://avuejs.com/robot/send', {
        tenant: 1,
        title: "Avue订单推送结果",
        list: [{
          label: "Avue订单推送结果",
          value: "Avue订单推送结果",
          bold: true,
        }, {
          label: "订单号",
          value: row.orderid
        }, {
          label: "用户QQ",
          value: row.qq
        }, {
          label: "套餐类型",
          value: row.$version,
        }, {
          label: "支付方式",
          value: row.$type
        }, {
          label: "授权名称",
          value: this.fpform.mc || '暂无'
        }, {
          label: "推送结果",
          value: "成功"
        }]
      })
    },
    refreshChange() {
      this.list = [];
      this.$message.success('刷新成功')
      this.getList();
    },
    getList(params) {
      if (this.validatenull(this.pwd)) return;
      const query = this.Bmob.Query("order");
      query.count().then(res => {
        this.page.total = res;
        const next = (this.page.currentPage - 1) * this.page.pageSize;
        query.limit(this.page.pageSize)
        query.skip(next)
        query.order("-datetime");
        for (let o in params) {
          query.equalTo(o, "==", params[o]);
        }
        return query.find()
      }).then(res => {
        this.list = res;
      });
      this.getMoneyData();
    }
  }
}
</script>

<style lang="scss">
.payList {
  padding: 100px 10px 100px 10px;
  min-height: 700px;
}

input[type=number] {
  width: 35px !important;
}
</style>
