<template>
  <div class="market">
    <div class="market__header">
      <el-menu
        default-active="1"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
        background-color="#051922"
        text-color="#fff"
        active-text-color="#ffd04b">
        <el-menu-item index="1">精选</el-menu-item>
        <el-menu-item index="2">系统插件</el-menu-item>
        <el-menu-item index="3">系统增强</el-menu-item>
        <el-menu-item index="4">应用平台</el-menu-item>
      </el-menu>
    </div>
    <div class="market__title">系统插件</div>
    <div class="market__search">
      <el-input placeholder="请输入完整授权编号查询(未收录或需要展示全名请联系作者)"
                @keyup.enter.native="handelSearch"
                v-model="value">
        <el-button type="primary"
                   slot="append"
                   @click="handelSearch">查询
        </el-button>
        <el-button type="primary"
                   slot="append"
                   @click="handelReset">重置
        </el-button>
      </el-input>
    </div>
    <div class="market__content"
         v-if="!validatenull(baseList)">
      <el-card class="market__text"
               v-for="(obj,index) in baseList"
               :key="index">
        <h3 slot="header">{{ obj.zsmc || '客户要求不展示名称' }}</h3>
        <p>授权编号：{{ obj.compid }}</p>
        <p>授权日期：{{ obj.datetime }}</p>
      </el-card>
    </div>
    <div class="market__content">
      <el-pagination
        background
        layout="prev, pager, next"
        :current-page="this.page.currentPage"
        :total="this.page.total"
        @current-change="currentChange">
      </el-pagination>
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      value: '',
      baseList: [],
      page: {
        currentPage: 1,
        total: 0,
        pageSize: 10
      }
    }
  },
  created() {
    this.getList();
  },
  methods: {
    handleSelect(key, keyPath) {
      window.console.log(key)
      window.console.log(keyPath)
    },
    currentChange(currentPage) {
      this.page.currentPage = currentPage;
      this.getList();
    },
    handelSearch() {
      this.page.currentPage = 1;
      this.getList();
    },
    handelReset() {
      this.value = '';
      this.handelSearch();
    },
    getList() {
      const query = this.Bmob.Query("ticket");
      query.select("zsmc,compid,datetime");
      query.limit(10);
      if (this.value) {
        query.equalTo("compid", "==", this.value.toUpperCase());
      }
      query.count().then(res => {
        this.page.total = res;
        const next = (this.page.currentPage - 1) * this.page.pageSize;
        query.limit(this.page.pageSize);
        query.skip(next);
        query.order("compid");
        query.find().then(res => {
          this.baseList = res.sort(function (x, y) {
            if (x.compid < y.compid) {
              return -1;
            } else if (x.compid > y.compid) {
              return 1;
            } else {
              return 0;
            }
          });
          this.baseList = this.baseList.filter(ele => {
            return ele.compid
          })
        });
      });
    }
  }
};
</script>

