<template>
  <div class="fp">
    <avue-crud :option="option"
               :data="data"
               :page="page"
               ref="crud"
               @search-change="searchChange"
               @search-reset="refreshChange"
               @size-change="sizeChange"
               @current-change="currentChange"
               @refresh-change="refreshChange"
               :row-style="rowStyle"
               @selection-change="selectionChange">
      <template slot="menuLeft">
        <el-button type="danger"
                   :icon="el-icon-document"
                   @click="$router.push({path:'/pay-list'})"
                   size="small">订单管理
        </el-button>
        <el-button type="primary"
                   icon="el-icon-s-order"
                   @click="setFlag"
                   size="mini">开 票
        </el-button>
      </template>
      <template slot-scope="scope"
                slot="menu">
        <el-button type="text"
                   @click="setBuild(scope.row)"
                   size="small">生成授权书
        </el-button>
      </template>
    </avue-crud>
    <auth ref="auth"
          @submit="getList">
    </auth>
  </div>
</template>

<script>
  import {mapState} from 'vuex';

  export default {
    data() {
      return {
        data: [],
        selectlist: [],
        page: {
          currentPage: 1,
          total: 0,
          pageSizes: [10, 30, 50, 100, 200, 1000],
          pageSize: 10
        },
        option: {
          size: 'mini',
          align: "center",
          menuAlign: 'left',
          headerAlign: 'center',
          index: true,
          excelBtn: true,
          selection: true,
          addBtn: false,
          delBtn: false,
          viewBtn: true,
          editBtn: false,
          tip: false,
          column: [{
            width: 120,
            label: '编号',
            prop: 'compid',
            search: true
          }, {
            width: 80,
            label: '金额',
            prop: 'money'
          }, {
            label: '公司名',
            prop: 'mc',
            search: true,
          }, {
            label: '税号',
            prop: 'sh',
          }, {
            width: 180,
            label: 'qq',
            search: true,
            prop: "qq"
          }, {
            width: 180,
            label: '日期',
            prop: 'datetime'
          }, {
            label: '备注',
            prop: 'bz'
          }]
        },
      }
    },
    computed: {
      ...mapState(['pwd']),
    },
    methods: {
      searchChange(params) {
        this.getList(params)
      },
      selectionChange(list) {
        this.selectlist = list;
      },
      setFlag() {
        this.selectlist.forEach(ele => {
          const query = this.Bmob.Query('ticket');
          query.get(ele.objectId).then(res => {
            res.set('flag', true)
            this.$set(this.data[ele.$index], 'flag', true)
            res.save()
          })
        });
        this.$refs.crud.toggleSelection();
        this.$message.success('设置成功')
      },
      rowStyle({row}) {
        if (!row.flag) {
          return {
            backgroundColor: 'rgba(245,108,108,0.1)'
          }
        }
      },
      setBuild(row) {
        const {href} = this.$router.resolve({
          path: '/sqs',
          query: {
            qq: row.qq,
            id: row.compid,
            name: row.mc,
            date: row.datetime
          }
        })
        window.open(href, '_blank')
      },
      currentChange(val) {
        this.page.currentPage = val;
        this.getList()
      },
      sizeChange(pageSize) {
        this.page.pageSize = pageSize;
        this.getList()
      },
      refreshChange() {
        this.data = [];
        this.$message.success('刷新成功')
        this.getList();
      },
      getList(params) {
        if (this.validatenull(this.pwd)) return;
        const query = this.Bmob.Query("ticket");
        query.count().then(res => {
          this.page.total = res;
          const next = (this.page.currentPage - 1) * this.page.pageSize;
          query.limit(this.page.pageSize)
          query.skip(next)
          query.order("-updatedAt");
          //query.order("-compid");
          for (let o in params) {
            query.equalTo(o, "==", params[o]);
          }
          return query.find()
        }).then(res => {
          /*res.forEach(ele => {
            ele.bz = "blade授权发票"
          });*/
          this.data = res;
        });
      }
    },
  }
</script>

<style lang="scss">
  .fp {
    padding-top: 100px;
    padding-bottom: 100px;
    min-height: 700px;
  }

  input[type=number] {
    width: 35px !important;
  }
</style>
