          <el-alert title="问：获取源码后发现自己不会用，可以退款么？" type="error"></el-alert>
          <el-alert title="答：我们提供的是全部源码，源码具有可复制性，所以一旦售出不支持退款，还请谅解。" type="success"></el-alert>
          <el-alert title="问：企业版授权有没有项目和服务器的限制？可以同时多少个人开发？" type="error"></el-alert>
          <el-alert title="答：购买授权后不限制项目和服务器，同一授权公司下的员工均可参与开发，没有限制。" type="success"></el-alert>
          <el-alert title="问：有没有技术支持，遇到问题了怎么办？" type="error"></el-alert>
          <el-alert title="答：购买授权后有一个名额进入商业群进行答疑，复杂问题也可以发到技术社区进行提问。不过没有一对一的实时私人技术支持，请谅解。" type="success"></el-alert>
          <el-alert title="问：如果需要一对一的技术支持，如何操作？" type="error"></el-alert>
          <el-alert title="答：看文档以及在商业群、社区提问能解决绝大多数问题。若还需要一对一技术支持，可以考虑另外付费，技术顾问市场价在2k-3k每天。" type="success"></el-alert>
          <el-alert title="问：基于BladeX开发的产品是否可以申请软著？" type="error"></el-alert>
          <el-alert title="答：可以用自己开发的业务代码申请软著，但不可以用BladeX核心源码申请软著。" type="success"></el-alert>
          <el-alert title="问：基于BladeX开发的产品对外销售，有没有什么限制？" type="error"></el-alert>
          <el-alert title="答：若对外销售的是服务，客户拿不到源码只使用系统，则没有限制。若对外销售带有源码，则需为客户购买一份企业授权，否则不可提供源码。" type="success"></el-alert>
          <el-alert title="问：为何给客户提供了源码就需要再次购买授权，不是已经买过了么？" type="error"></el-alert>
          <el-alert
            title="答：企业版授权以公司为一个主体，客户属于另一个主体，若客户拿到了源码，则需要购买一次授权。购买授权后，这家客户后续的所有项目都无需再购买授权了，也就是说，一个主体只需购买一次授权。若A单位购买了授权，将源码泄露给没有购买授权的B单位，哪怕两者有生意往来也是不行的，否则这就成为了盗版漏洞。比如有人购买一次授权，若没有授权单位的限制，他就可以在网上超低价销售源码给任何人，对外声称购买源码的人都是自己的客户，这会对作者以及正版用户带来巨大损失，所以这种盗版行为肯定是不被允许的。"
            type="success"></el-alert>
          <el-alert title="问：盗版侵权，有没有什么严重后果？" type="error"></el-alert>
          <el-alert
            title="答：盗版的源码不是从官方获取，会有可能被人植入后门，给项目带来严重隐患。盗版使用者是没法获得官方技术支持的，版本遗留的bug无法更新，使用碰到问题无法获得解决，光这一块就会耗费大量精力，付出的代价甚至会比购买授权高很多。盗版行为不受法律保护，如果情节严重，版权方起诉侵犯软件著作权，盗版发布者和盗版使用者几乎没可能胜诉，一旦留下案底将影响自己的大好前程，也将影响自己未来子女的前途。盗版百害无一益，一定慎重，否则一旦出事，将无法挽回。"
            type="success"></el-alert>
        </el-tab-pane>
        <el-tab-pane label="软著证书">
          <img src="https://box.kancloud.cn/3b429dbb490cb45bb5c115a4a36965c8_1348x1840.png" width="100%" alt="">
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
    <div id="qrcode"
         style="display:none"
         ref="qrcode"></div>
  </div>

</template>

<script>
  import {config, payList} from './config'
  import {Encrypt} from '../util/crypto';
  import Clipboard from 'clipboard';

  const prefixCls = "pay";

  var validateQQ = (rule, value, callback) => {
    if (value.length < 5) {
      callback(new Error('QQ号不正确'));
    } else {
      callback();
    }
  };
  export default {
    data() {
      return {
        config: config,
        loading: false,
        img: '',
        payImg: '/images/loading.gif',
        box1: false,
        box2: false,
        fpbox: false,
        datetime: '',
        datecheck: null,
        ordercheck: null,
        fpform: {},
        fpoption: {
          emptyBtn: false,
          column: [{
            label: '订单号',
            prop: 'orderid',
            disabled: true,
            span: 24
          }, {
            label: '公司名称',
            prop: 'mc',
            rules: [{
              required: true,
              message: "请输入公司名称",
              trigger: "blur"
            }],
            span: 24
          }, {
            label: '公司税号',
            prop: 'sh',
            rules: [{
              required: true,
              message: "请输入公司税号",
              trigger: "blur"
            }],
            span: 24
          }
            /*, {
              label: '收件人',
              prop: 'sjr',
              span: 24
            }, {
              label: '联系方式',
              span: 24,
              prop: 'lxfs',
            }, {
              label: '收件地址',
              prop: 'sjdz',
              type: 'textarea',
              span: 24
            }*/
            , {
              label: '备注',
              prop: 'bz',
              span: 24
            }]
        },
        box: false,
        cBox: false,
        list: config,
        id: '',
        form: {
          orderid: '',
          datetime: '',
          day: 0,
          type: 1,
          qq: '',
          flag: false,
        },
        option: {
          menuPostion: 'left',
          emptyBtn: false,
          submitBtn: false,
          column: [{
            label: '',
            labelWidth: 1,
            formslot: true,
            row: true,
            prop: 'default'
          }, {
            label: '产品名称',
            formslot: true,
            row: true,
            prop: 'name'
          }, {
            label: '产品类型',
            type: 'radio',
            border: true,
            formslot: true,
            dicData: config,
            span: 24,
            row: true,
            prop: 'version'
          }, {
            label: '授权期限',
            type: 'radio',
            border: true,
            display: false,
            dicData: [{
              label: '永久',
              value: 0
            }],
            row: true,
            prop: 'day'
          }, {
            label: '授权须知',
            formslot: true,
            row: true,
            prop: 'defaults'
          }, {
            label: '',
            formslot: true,
            labelWidth: 1,
            row: true,
            prop: 'money'
          }, {
            label: '支付方式',
            type: 'radio',
            border: true,
            dicData: payList,
            formslot: true,
            row: true,
            prop: 'type'
          }, {
            label: '联系QQ',
            span: 7,
            row: true,
            rules: [{
              required: true,
              message: "请输入联系QQ",
              trigger: "blur"
            },
              {
                validator: validateQQ,
                trigger: 'blur'
              }],
            prop: 'qq'
          }, {
            label: '购买说明',
            formslot: true,
            row: true,
            prop: 'introduction'
          }]
        }
      }
    },
    watch: {
      $route() {
        this.init();
      },
      'box'(val) {
        if (!val) {
          clearInterval(this.ordercheck);
          clearInterval(this.datecheck);
        }
      },
      'form.version': {
        handler(val) {
          if (this.form.version === 0 || this.form.version === 2) {
            this.form.type = 1;
          }
          const obj = config.filter(ele => ele.value === val)[0];
          this.form.name = obj.label;
          this.form.money = obj.money;
          this.$router.push({
            path: '/pay',
            query: {
              version: val
            }
          })
        }
      }
    },
    created() {
      this.init();
    },
    mounted() {

    },
    computed: {
      datetimeList() {
        return this.datetime.split('')
      },
      className() {
        return this.form.type === 2 ? `${prefixCls}--bule` : `${prefixCls}--green`
      },
      btncolor() {
        return this.form.type === 2 ? 'primary' : 'success'
      },
      imgsrc() {
        return this.form.type === 2 ? '/images/zgyhicon.png' : ' /images/wxicon.png'
      },
      text() {
        return this.form.type === 2 ? '对公' : '微信'
      }
    },
    methods: {
      copy(className) {
        let clipboard = new Clipboard('.' + className);
        clipboard.on('success', e => {
          window.console.log(e);
          this.$message.success('复制成功');
          // 释放内存
          clipboard.destroy();
        });
        clipboard.on('error', e => {
          // 不支持复制
          window.console.log(e);
          this.$message.success('复制失败，请用其他浏览器尝试');
          // 释放内存
          clipboard.destroy()
        })
      },
      isCompany() {
        return this.form.type === 2;
      },
      handleEmpty(){
        this.fpform.mc = '';
        this.fpform.sh = '';
        this.fpform.bz = '';
      },
      handleSelect(item) {
        if (item.href) {
          window.location.href = item.href;
          return
        }
        this.form.version = item.value;
      },
      init() {
        this.form.version = Number(this.$route.query.version)
        if (!this.form.version) this.form.version = 0
      },
      insert(data, table) {
        const query = this.Bmob.Query(table || 'order');
        Object.keys(data).forEach(ele => {
          if (ele.includes('$')) {
            query.set(ele.replace('$', '') + '_text', data[ele])
          } else {
            query.set(ele, data[ele])
          }
        });
        return query.save()
      },
      update(id, data, table) {
        const form = this.deepClone(data);
        const query = this.Bmob.Query(table || 'order');
        query.set('id', id)
        delete form.createdAt;
        delete form.updatedAt;
        delete form.objectId;
        delete form.key;
        delete form.introduction;
        Object.keys(form).forEach(ele => {
          if (form[ele]) {
            query.set(ele, form[ele]) //需要修改的objectId
          }

        })
        return query.save()
      },
      dingPush() {
        this.$httpajax.post('https://avuejs.com/sendmsg', {
          qq: this.form.qq,
          type: this.form.$type,
          version: this.form.$version,
          money: this.form.money,
          tenant: 1,
          orderid: this.form.orderid
        })
      },
      tradeNo() {
        const now = new Date()
        const year = now.getFullYear();
        let month = now.getMonth() + 1;
        let day = now.getDate();
        let hour = now.getHours();
        let minutes = now.getMinutes();
        let seconds = now.getSeconds();
        String(month).length < 2 ? (month = "0" + month) : month;
        String(day).length < 2 ? (day = "0" + day) : day;
        String(hour).length < 2 ? (hour = "0" + hour) : hour;
        String(minutes).length < 2 ? (minutes = "0" + minutes) : minutes;
        String(seconds).length < 2 ? (seconds = "0" + seconds) : seconds;
        const yyyyMMddHHmmss = `${year}${month}${day}${hour}${minutes}${seconds}`;
        this.id = Math.random().toString(36).substr(2, 6);
        return yyyyMMddHHmmss + '_' + this.id
      },
      handleBox() {
        if (this.form.version === 0) {
          this.box1 = true;
        } else {
          this.box2 = true;
        }
      },
      handleFpSubmit(form, done) {
        this.fpform.flag = false;
        this.$confirm(`确认提交发票信息，一经提交无法修改？`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.insert(this.fpform, 'ticket').then(() => {
            this.$httpajax.post('https://avuejs.com/sendfp',
              Object.assign(this.fpform, this.form,
                {
                  tenant: 1,
                  sjr: '-',
                  lxfs: '-',
                  sjdz: '-'
                }));
            this.fpbox = false;
            this.$message.success('发票提交成功');
            this.msgtip();
            done();
          })
        }).catch(() => {
          done();
        })
      },
      queryOrderid() {
        clearInterval(this.ordercheck);
        this.ordercheck = setInterval(() => {
          const query = this.Bmob.Query('order')
          query.equalTo("orderid", "==", this.form.orderid);
          query.find().then(res => {
            const flag = res[0].flag;
            if (flag) {
              this.box = false;
              if ([1, 2, 3].includes(this.form.version)) {
                this.fpbox = true;
              } else {
                this.msgtip();
              }
              if (this.box === false) {
                clearInterval(this.ordercheck);
                clearInterval(this.datecheck);
              }
            }
          });
        }, 1000);
      },
      msgtip() {
        this.$alert(`相关资料将发送至${this.form.qq}@qq.com邮箱请注意查收`, '提示', {
          confirmButtonText: '确定',
          callback: () => {
            this.$router.push('/doc/notice')
          }
        });
      },
      queryTime() {
        this.datetime = "05:00";
        var maxtime = 5 * 60 - 1; //
        const CountDown = () => {
          if (maxtime >= 0) {
            this.update(this.form.objectId, {
              datetime: new Date((new Date().getTime() - 1000 * 20)).Format('yyyy-MM-dd hh:mm:ss')
            })
            var minutes = Math.floor(maxtime / 60);
            var seconds = Math.floor(maxtime % 60);
            if (minutes < 10) minutes = '0' + minutes
            if (seconds < 10) seconds = '0' + seconds
            this.datetime = minutes + ":" + seconds
            --maxtime;
          } else {
            this.box = false;
            clearInterval(this.datecheck);
          }
        };
        clearInterval(this.datecheck);
        this.datecheck = setInterval(() => CountDown(), 1000);
      },
      loadPayImg() {
        this.payImg = "/images/loading.gif";
        const datetime = new Date().Format('yyyyMMddhhmmss');
        this.$httpajax.post(this.website.PAY.url, Object.assign(this.form, {key: Encrypt(datetime)})).then(resp => {
          if (resp.data.code === "200") {
            this.payImg = resp.data.data;
          } else {
            this.$message.error("网络繁忙，请稍后再试。");
          }
        });

      },
      handleSubmit(form, done) {
        done();
        this.form.qq = this.form.qq.trim();
        let numReg = /^[0-9]+$/;
        let numRe = new RegExp(numReg);
        if (!numRe.test(this.form.qq)) {
          this.$message.error('请输入正确的qq号！');
          return false;
        }
        this.loading = true;
        const query = this.Bmob.Query("order");
        query.equalTo("qq", "==", this.form.qq);
        query.equalTo("type", "==", this.form.type);
        query.equalTo("version", "==", this.form.version);
        query.equalTo("flag", "==", false);
        query.find().then(res => {
          if (this.validatenull(res[0])) {
            this.form.orderid = this.tradeNo();
            this.fpform.orderid = this.form.orderid;
            this.form.datetime = (new Date()).Format('yyyy-MM-dd hh:mm:ss');
            if (this.form.qq.indexOf("@qq.com") > 0) {
              this.form.qq = this.form.qq.replace("@qq.com", "");
            }
            let data = this.deepClone(this.form)
            delete data.objectId;
            delete data.createdAt;
            delete data.updatedAt;
            delete data.key;
            delete data.introduction;
            // 如果是联合版，则改为bladex价格
            if (data.version === 3) {
              data.money = 3500;
            }
            this.insert(data).then((res) => {
              this.form.objectId = res.objectId;
              this.dingPush();
              if (this.isCompany()) {
                this.cBox = true;
              } else {
                this.box = true;
                this.loadPayImg();
                this.queryOrderid();
                this.queryTime();
              }
              done();
              this.loading = false;
            });
          } else {
            let data = res[0];
            // 如果是联合版，则改回总体价格
            if (data.version === 3) {
              data.money = config[1].money;
            }
            this.form = data;
            // 更新下单时间
            this.update(this.form.objectId, {
              datetime: (new Date()).Format('yyyy-MM-dd hh:mm:ss')
            });
            this.fpform.orderid = this.form.orderid;
            this.id = this.form.orderid.split("_")[1];
            if (this.isCompany()) {
              this.cBox = true;
            } else {
              this.box = true;
              this.loadPayImg();
              this.queryOrderid();
              this.queryTime();
            }
            done();
            this.loading = false;
          }
        });


      }
    }
  }
</script>

